/* eslint-disable react/jsx-props-no-spreading */
import React, { ComponentType } from 'react';
import { Route, Redirect, RouteProps } from 'react-router-dom';
import useSWR from 'swr';

import LoadingPage from 'pages/loading';
import checkUserLogged from 'app/utils/checkUserLogged';
import ROUTES from 'app/routes';

interface Props {
  component: ComponentType;
  restricted?: boolean;
}

const PublicRoute = ({ component: Component, restricted, ...rest }: Props & RouteProps) => {
  const { data: userConnected, error } = useSWR('userConnected', checkUserLogged);

  if (userConnected !== undefined && !error) {
    return (
      <Route
        {...rest}
        render={(props) =>
          userConnected && restricted ? (
            <Redirect to={ROUTES.DASHBOARD} />
          ) : (
            <Component {...props} />
          )
        }
      />
    );
  }

  if (error) return <Redirect to={ROUTES.LOGIN} />;

  return <LoadingPage />;
};

export default PublicRoute;
