import { Magic } from 'magic-sdk';

class MagicService {
  private static instance: MagicService;

  private magic: Magic;

  private constructor() {
    this.magic = new Magic(process.env.REACT_APP_MAGIC_SDK_KEY || '', { network: 'ropsten' });
  }

  public static getInstance(): MagicService {
    if (!MagicService.instance) {
      MagicService.instance = new MagicService();
    }
    return MagicService.instance;
  }

  public getRpcProvider() {
    return this.magic.rpcProvider;
  }

  public getUserInfos() {
    return this.magic.user.getMetadata();
  }

  public async login(email: string): Promise<string | null> {
    return this.magic.auth.loginWithMagicLink({ email });
  }

  public async logout(): Promise<boolean> {
    return this.magic.user.logout();
  }

  public async isUserLoggedIn(): Promise<boolean> {
    return this.magic.user.isLoggedIn();
  }
}

export default MagicService;
