import React from 'react';
import useSWR from 'swr';
import { Skeleton } from 'antd';

import MagicService from 'app/services/MagicService';

const Email = () => {
  const getEmail = async () => {
    const magicService = MagicService.getInstance();
    return magicService.getUserInfos();
  };

  const { data, error } = useSWR('email', getEmail);

  if (error) {
    return null;
  }

  if (!data) {
    return <Skeleton.Input style={{ width: 200, marginTop: 15 }} active />;
  }

  return <div>{data.email}</div>;
};

export default Email;
