import { Breadcrumb } from 'antd';
import ROUTES from 'app/routes';
import React, { ReactNode } from 'react';
import { Link, useLocation } from 'react-router-dom';

import { worksystems } from '../Worksystems';
import styles from './WorksystemLayout.module.css';

interface Props {
  children: ReactNode;
}

const WorksystemLayout = ({ children }: Props) => {
  const Content = () => <div className={styles.content}>{children}</div>;
  const { pathname } = useLocation();
  const foundWorkystem = worksystems.find((worksystem) => worksystem.link === pathname);
  if (!foundWorkystem) return <Content />;

  const { id, title } = foundWorkystem;
  return (
    <>
      <Breadcrumb>
        <Breadcrumb.Item key={id}>
          <Link to={ROUTES.WORKSYSTEMS.INDEX}>Worksystems</Link>
        </Breadcrumb.Item>
        <Breadcrumb.Item>{title}</Breadcrumb.Item>
      </Breadcrumb>
      <Content />
    </>
  );
};

export default WorksystemLayout;
