import React from 'react';
import { Card, Row, Col } from 'antd';

import spotting from 'assets/img/worksystems/spotting.jpg';
import moderating from 'assets/img/worksystems/moderating.jpg';
import ROUTES from 'app/routes';
import { Link } from 'react-router-dom';

import styles from './Worksystems.module.css';

interface Worksystem {
  id: string;
  link: string;
  img: string;
  title: string;
  description: string;
}

export const worksystems: Worksystem[] = [
  {
    id: 'spotting',
    link: ROUTES.WORKSYSTEMS.SPOTTING,
    img: spotting,
    title: 'Spotting',
    description: 'Prioritize information for Exorde by voting on the most viral news.',
  },
  {
    id: 'moderating',
    link: ROUTES.WORKSYSTEMS.MODERATING,
    img: moderating,
    title: 'Moderating',
    description: 'Moderate spotted content so as to ensure its relevance to Exorde.',
  },
];

const WorksystemsPage = () => {
  const { Meta } = Card;
  return (
    <div className={styles.container}>
      <Row gutter={16}>
        {worksystems.map((worksystem) => (
          <Col span={6} key={worksystem.id}>
            <Link to={worksystem.link}>
              <Card
                hoverable
                style={{ width: 240 }}
                cover={
                  <img alt={worksystem.title} src={worksystem.img} className={styles.cardImage} />
                }>
                <Meta title={worksystem.title} description={worksystem.description} />
              </Card>
            </Link>
          </Col>
        ))}
      </Row>
    </div>
  );
};

export default WorksystemsPage;
