import React, { useState } from 'react';
import useSWR from 'swr';
import { Button, Tooltip } from 'antd';
import { LogoutOutlined } from '@ant-design/icons';

import MagicService from 'app/services/MagicService';
import { Redirect } from 'react-router-dom';
import ROUTES from 'app/routes';

import styles from './Logout.module.css';

const Logout = () => {
  const [shouldLogout, setShouldLogout] = useState(false);
  const logout = async () => {
    const magicService = MagicService.getInstance();
    return magicService.logout();
  };

  const { data, error } = useSWR(shouldLogout ? 'logout' : null, logout);

  if (error) return <Redirect to={ROUTES.LOGIN} />;
  if (data) return <Redirect to={ROUTES.LOGIN} />;

  return (
    <>
      <Tooltip placement="topRight" title="Logout">
        <Button
          type="primary"
          icon={<LogoutOutlined />}
          size="large"
          onClick={() => setShouldLogout(true)}
          className={styles.button}
          ghost
        />
      </Tooltip>
    </>
  );
};

export default Logout;
