import React, { ReactNode, useState } from 'react';
import { Layout as AntDesignLayout, Menu } from 'antd';
import {
  MenuUnfoldOutlined,
  MenuFoldOutlined,
  DashboardOutlined,
  PlayCircleOutlined,
} from '@ant-design/icons';

import ROUTES from 'app/routes';
import { Link } from 'react-router-dom';
import styles from './Layout.module.css';
import Email from './Email/Email';
import Logout from './Logout';

interface Props {
  children?: ReactNode;
}

interface MenuItem {
  id: string;
  label: string;
  icon: any;
  link: string;
}

const menuItems: MenuItem[] = [
  {
    id: 'dashboard',
    label: 'Dashboard',
    icon: DashboardOutlined,
    link: ROUTES.DASHBOARD,
  },
  {
    id: 'worksystems',
    label: 'Worksystems',
    icon: PlayCircleOutlined,
    link: ROUTES.WORKSYSTEMS.INDEX,
  },
];

const Layout = ({ children }: Props) => {
  const { Header, Sider, Content } = AntDesignLayout;
  const [collapsed, setCollapsed] = useState(false);
  return (
    <AntDesignLayout className={styles.container}>
      <Sider trigger={null} collapsible collapsed={collapsed} breakpoint="md">
        <div className={styles.logo} />
        <Menu theme="dark" mode="inline" defaultSelectedKeys={['1']}>
          {menuItems.map(({ icon: Icon, label, link }, index) => (
            <Menu.Item key={`${index + 1}`} icon={<Icon />}>
              <Link to={link}>{label}</Link>
            </Menu.Item>
          ))}
        </Menu>
        <Logout />
      </Sider>
      <AntDesignLayout>
        <Header className={styles.appbar}>
          {React.createElement(collapsed ? MenuUnfoldOutlined : MenuFoldOutlined, {
            className: styles.trigger,
            onClick: () => setCollapsed(!collapsed),
          })}
          <Email />
        </Header>
        <Content className={styles.content}>{children}</Content>
      </AntDesignLayout>
    </AntDesignLayout>
  );
};

export default Layout;
