import React, { useState } from 'react';
import useSWR, { mutate } from 'swr';
import { Redirect, useLocation } from 'react-router-dom';

import MagicService from 'app/services/MagicService';
import isValidEmail from 'app/utils/isValidEmail';
import ROUTES from 'app/routes';
import LoginScreen from './Login.screen';

const Login = () => {
  const [loginEmail, setLoginEmail] = useState('');
  const [shouldCallLoginService, setShouldCallLoginService] = useState(false);

  const location = useLocation();
  const { from }: any = location.state || {
    from: { pathname: ROUTES.DASHBOARD },
  };

  const handleLogin = async () => {
    const magicService = MagicService.getInstance();
    return magicService.login(loginEmail);
  };

  const { data, error } = useSWR(
    isValidEmail(loginEmail) && shouldCallLoginService ? loginEmail : null,
    handleLogin,
  );

  const handleConnectionClick = (email: string) => {
    setLoginEmail(email);
    setShouldCallLoginService(true);
    mutate(email);
  };

  const loading = shouldCallLoginService && !data;

  if (error) {
    setShouldCallLoginService(false);
  }

  if (data) {
    return <Redirect to={from.pathname} />;
  }

  return <LoginScreen loading={loading} onConnectionClick={handleConnectionClick} />;
};

export default Login;
